export default function (Component) {
       if(!Component.options.wxsCallMethods){
         Component.options.wxsCallMethods = []
       }
       Component.options.wxsCallMethods.push('setWxsPropAllow')
Component.options.wxsCallMethods.push('setWxsPropPending')
Component.options.wxsCallMethods.push('setWxsPropTip')
     }